.navbar{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding: 10px;

  /* background-color: #1E2B31; */
}

.navbar-list{
  list-style: none;
  margin: 10px;
}

.navbar-list a{
	list-style: none;
  text-decoration: none;
  color: whitesmoke;
}

.navbar-list a:hover{
  text-decoration: underline;
  text-decoration-style: wavy;
}

.navbar-list a:focus{
  text-decoration: underline;
  text-decoration-style: wavy;
}